import React from 'react';
import PropTypes from 'prop-types';

export function Slideshow(props) {
    let src = props.src;
    return (
      <div>
        <h1>Cute Guinea Pigs</h1>
        <img src={src} alt="slide" />
      </div>
    );
}

Slideshow.propTypes = {
    src: PropTypes.string.isRequired
}